<template>
    <view class="integral_page">
        <!-- tabbar -->
        <cu-custom><block slot="content">馈赠分充值</block></cu-custom>
        <view class="things_width_full flex justify-start align-center use_money">1积分 = 1元</view>
        <view class="flex justify-center align-start input_value_box">
            <view class="input_value_title">充值积分</view>
            <view class="flex justify-start align-center input_box">
                <view class="input_label">￥</view>
                <input class="integral_money" type="digit" placeholder="输入金额" :value="pay_money" @input="payMoneyInput" />
            </view>
        </view>
        <!-- 支付选择选项 -->
        <view class="things_width_full pay_type">
            <view class="flex justify-between align-center pay_type_item" data-id="4" @tap="selectPayTypeFun">
                <view class="flex justify-start align-center pay_type_item_left">
                    <image class="pay_icon" src="./../../../../../static/images/global/pay_icon1.png" mode="widthFix" />
                    <view class="pay_name">余额支付<text class="balance">(总余额：{{userAmount.balance}}元)</text></view>
                </view>
                <view class="pay_type_item_right">
                    <image v-if="selectIndex === 4" class="select_icon" src="./../../../../../static/images/global/select1.png" mode="widthFix" />
                    <image v-else class="select_icon" src="./../../../../../static/images/global/select0.png" mode="widthFix" />
                </view>
            </view>
            <view class="flex justify-between align-center pay_type_item" data-id="1" @tap="selectPayTypeFun">
                <view class="flex justify-start align-center pay_type_item_left">
                    <image class="pay_icon" src="./../../../../../static/images/global/pay_icon2.png" mode="widthFix" />
                    <view class="pay_name">微信支付</view>
                </view>
                <view class="pay_type_item_right">
                    <image v-if="selectIndex === 1" class="select_icon" src="./../../../../../static/images/global/select1.png" mode="widthFix" />
                    <image v-else class="select_icon" src="./../../../../../static/images/global/select0.png" mode="widthFix" />
                </view>
            </view>
        </view>
        <!-- 支付按钮 -->
        <view class="flex justify-center align-center btn_box">
            <button class="cu-btn integral_btn" :disabled="buttonType" @tap="giftPointRechargeFun">充值</button>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { giftsPointsFraction_api,giftPointsRecharge_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import storage from '@/utils/storage.js';
import payFun from '@/utils/pay';
let that = null;
export default {
    data() {
        return {
            userAmount: {},// 积分分数
            buttonType: false,
            pay_money: '',// 充值的数量
            selectIndex: 4,// 选择的支付方式
        }
    },
    watch: {
        '$store.getters.user_userData': function(val) {
            that.userData = val;
        },
    },
    methods: {
        ...mapMutations([
            'modal',
            'user'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        clearPageFun(e) {
            that.pay_money = '';
            that.getFraction();
        },
        /**选择支付方式
         * @description: 
         * @param {*} e
         * @return {*}
         */
        selectPayTypeFun(e) {
            if (Number(e.currentTarget.dataset.id) === that.selectIndex) return false;
            that.selectIndex = Number(e.currentTarget.dataset.id);
        },
        /**馈赠分充值
         * @description: 
         * @param {*} e
         * @return {*}
         */
        giftPointRechargeFun(e) {
            if (!that.pay_money || Number(that.pay_money) === 0) {
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'请输入充值金额'}});
                return false;
            };
            that.buttonType = true;
            that.modal({name:'popLoading',value:true});
            let params = {
                pay_money: that.pay_money,
                channel_type: that.selectIndex
            };
            giftPointsRecharge_api(params,that).then(res=>{
                if (that.selectIndex === 1) {
                    payFun.wxPay(res.payment,that).then(res=>{
                        that.modal({name:'popLoading',value:false});
                        that.buttonType = false;
                        that.modal({name:'popTipModal',value:{type:true,tipTitle:'恭喜',tipContent:'充值成功'}});
                        that.clearPageFun();
                    }).catch(err=>{
                        console.log(err);
                        that.modal({name:'popLoading',value:false});
                        that.buttonType = false;
                    })
                }
                if (that.selectIndex === 4) {
                    that.modal({name:'popLoading',value:false});
                    that.buttonType = false;
                    that.modal({name:'popTipModal',value:{type:true,tipTitle:'恭喜',tipContent:'充值成功'}});
                    that.clearPageFun();
                }
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
                that.modal({name:'popLoading',value:false});
            })
        },
        /**修改挂售建设分数量
         * @description: 
         * @param {*} e
         * @return {*}
         */
        payMoneyInput(e) {
            that.pay_money = e.detail.value;
        },
        /**获取积分分数
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFraction(e) {
            let params = {};
            giftsPointsFraction_api(params,that).then(res=>{
                that.userAmount = res.userAmount;
            }).catch(err=>{
                console.log(err);
            })

        },
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
    },
    onShow: function() {
        that.clearPageFun();
    }
}
</script>
<style lang="scss" scoped>
    @import "./index.scss";
</style>